@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;700&display=swap);
html {
  background-color: #FDF1E9;
}
body {
  margin: 0;
  font-family: 'Raleway', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #FDF1E9;
  color: #4B4F3F;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
  background-color: #FDF1E9;
  color: #4B4F3F;
}

.customOverlay {
  background: rgba(255, 218, 203, 0.6) !important;
}
.customModal {
  background-color: #FDF1E9;
  width: 30%;
  padding: 15%;
}

#userInfoFormModal {
  border-radius: 50px;
  padding: 15%;
}

input[type="text"],
textarea {
  height: 36px;
  width: 90%;
  padding: 10px;
  margin-bottom: 3%;
  margin-top: 1%;
  padding: 3%;
  border: 1px solid #cb997e;
  border-radius: 4px;
}

.home-image {
  width: 70%;
  height: auto;
}


select {
  height: 40px;
  width: 90%;
  border: 1px solid #cb997e;
  border-radius: 4px;
  align-self: center;
  color: #4B4F3F;
  margin-bottom: 3%;
  padding: 1%;
}

.product-header {
    display: flex;
    justify-content: space-between;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 1rem;
}

.category-header {
    display: flex;
    justify-content: space-between;
    padding-left: 3rem;
    padding-right: 3rem;
    padding-top: 2rem;  
}

.product-card {
    max-width: 18rem;
    min-width: 18rem;
}

.product-header-filter {
    display: flex;  
}

.filter-buttons {
    display: flex;
}

.product-form {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.product-category-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.App {
  text-align: center;
  background-color: #FDF1E9;
  color: #4B4F3F;
}


.container {
  margin-top: 10%;
  width: 100%;
  display: flex;
  justify-content: center;
}

.formDiv {
  width: 400px;
  padding: 30px;
  border-radius: 10px;
  box-shadow: -1px 8px 15px 0px rgba(0, 0, 0, 0.44);
}

.alert {
  text-align: center;
  margin-top: 20px;
}

.creditCard{
    margin-bottom: 10%;
}

