.product-header {
    display: flex;
    justify-content: space-between;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 1rem;
}

.category-header {
    display: flex;
    justify-content: space-between;
    padding-left: 3rem;
    padding-right: 3rem;
    padding-top: 2rem;  
}

.product-card {
    max-width: 18rem;
    min-width: 18rem;
}

.product-header-filter {
    display: flex;  
}

.filter-buttons {
    display: flex;
}

.product-form {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.product-category-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}
